<template>
  <div class="flex justify-between items-center mb-8">
    <Breadcrumb
      pageTitle="Edit Notification"
      :backTo="{
        name: 'settingEmailTemplate',
        query: {
          filter: 1
        }
      }"
      :items="breadcrumb"
    />
    <Button
      color="teal"
      :loading="loading.update"
      @click="update"
      >Save Changes</Button
    >
  </div>
  <Card>
    <div class="space-y-6">
      <SectionTitle section="1"
        >Set Trigger</SectionTitle
      >
      <div class="md:ml-16 space-y-6">
        <div class="text-md">
          Set a trigger to automatically send review emails for offers that have been marked completed, based on the payout methods you offer (select all that apply).
        </div>
        <div v-for="groupKey in sortedGroups" :key="groupKey">
      <ExpandablePanel>
        <template #header="{ toggle, isExpanded }">
          <div @click="toggle" class="grid grid-cols-4 items-center cursor-pointer">
            <div class="col-span-4 md:col-span-1 flex gap-x-4 items-center">
              <div class='el-table__expand-icon'>
                <i class="el-icon-arrow-right" />
              </div>
              <p class="font-semibold">{{ groupKey }}</p>
            </div>

            <p class="col-span-4 md:col-span-3">{{ groupDescriptions[groupKey] }}</p>
          </div>
        </template>
        
        <template #default>
          <Table :data="data.payment_method[groupKey]">
            <TableColumn label="Payout Method" width="200">
              <template #default="scope">
                <div class="flex items-center gap-4">
                  <img
                    :src="scope.row.icon"
                    class="w-9"
                    :alt="scope.row.name"
                    />
                    <p>{{ scope.row.name }}</p>
                </div>
              </template>
            </TableColumn>
            <TableColumn label="Trigger Time">
              <template #default="scope">
                <div class="flex flex-wrap sm:flex-nowrap items-center gap-2">
                  <Input
                    v-model="scope.row.review_delay"
                    @keypress="filterNumber"
                    dense
                    class="sm:w-14"
                  />
                  <Select
                    v-model="scope.row.delay_type"
                    :items="items"
                    name="name"
                    by="value"
                    class="w-56 sm:w-28"
                    dense
                    />
                </div>
              </template>
            </TableColumn>
          </Table>
        </template>
      </ExpandablePanel>
    </div>
      </div>
      <SectionTitle section="2"
        >Set Review Link</SectionTitle
      >
      <div class="md:ml-16">
        <div class="flex items-center gap-2 mb-2">
          <p class="font-semibold">
            Link
          </p>
          <Tooltip placement="top">
            <template #content>
              <div :style="{ maxWidth: '300px' }">
                Add the Url that directs customers to your business's review page, where they can leave feedback about their experiences.
              </div>
            </template>
            <template #default>
              <i
                class="material-icons cursor-default"
                style="line-height: 1!important;">
                info
              </i>
            </template>
          </Tooltip>
        </div>
        <Input
          v-model="data.web_link"
          type="text"
          class="w-full md:w-1/2"
          :view="(validation.web_link.$invalid && 'danger') || 'default'"
        />
        <small
          class="text-red-base block"
          v-for="(error, errorIndex) of validation.web_link.$errors"
          :key="errorIndex"
          >{{ error.$message }}.
        </small>
      </div>
      <SectionTitle section="3"
        >Set Email Template</SectionTitle>
        <div class="md:ml-16">
          <Input
            v-model="formDataCompany.email_sender"
            class="mb-4 w-full md:w-1/2"
            label="From"
            disabled
          />
          <div class="mb-3 mt-3 w-full md:w-1/2">
            <label>BCC</label>
            <MultipleEmailInput
              @email-change="handleBccUpdate"
              :suggestion="false"
              :emails="data.email_wording.bcc"
            />
          </div>
          <Input
            v-model="data.email_wording.subject"
            label="Subject"
            class="w-full md:w-1/2"
            :view="(validation.email_wording.subject.$invalid && 'danger') || 'default'"
          />
          <small
            class="text-red-base block"
            v-for="(error, errorIndex) of validation.email_wording.subject.$errors"
            :key="errorIndex"
          >
            {{ error.$message }}.
          </small>
          <label class="block mb-2 mt-4">Logo</label>
          <div>
            <Checkbox
              v-model="formDataEmailTemplate.is_review_mail_logo_show"
              @input="handleUpdateEmailTemplateTheme"
              :loading="loadingEmailTemplateTheme.update"
              type="toggle"
              id="logo"
              :value="1"
              :value-off="0"
            >
              <span class="font-medium">Show company logo</span>
            </Checkbox>
          </div>
          <div class="mt-4 col-span-4">
            <TextEditor
              ref="refTextEditor"
              v-model="data.email_wording.textinfo"
              :options="ckeditor"
            >
              <template #toolbar>
                <div class="flex items-center lg:ml-auto">
                  <Button
                    color="teal"
                    :loading="
                      loadingEmailTemplateTheme.update ||
                      loadingPreviewEmailTemplate
                    "
                    @click="handleResetEmailTemplate"
                    :disabled="data.email_wording.textinfo === data.email_wording.initialTextinfo"
                    dense
                    view="smooth"
                  >
                    <i class="material-icons mr-1">refresh</i>
                    Reset
                  </Button>
                  <Button
                  class="ml-2"
                  @click="handlePreviewEmailTemplate"
                  :loading="
                    loadingPreviewEmailTemplate ||
                    loadingEmailTemplateTheme.update
                    "
                  color="teal"
                  view="outlined"
                  dense
                  >
                  <i class="material-icons mr-1">visibility</i>
                  Preview
                </Button>
              </div>
              </template>
            </TextEditor>
          </div>
          <Table :data="data.email_wording.sort_code" class="mt-6">
            <TableColumn label="Code">
              <template #default="scope">
                <div class="flex">
                  <span class="mr-2">{{ scope.row.code }}</span>
                  <i
                    class="material-symbols-outlined text-lg text-teal-04"
                    @click="handleCopyText(scope.row.code)"
                  >
                    content_copy
                  </i>
                </div>
              </template>
            </TableColumn>
            <TableColumn prop="description" label="Description" />
          </Table>
        </div>
    </div>
  </Card>
  <PreviewEmail v-model:show="isPreview" :body="previewedTemplate" />
</template>

<script>
import { defineComponent, reactive, watch, ref, computed } from 'vue'
import { filterNumber } from 'utils/number'
import { useReviewEmail, useCompanyProfile, useEmailTemplateTheme, usePreviewEmailTemplate } from 'composable/setting'
import { deepClone } from 'utils/deep-clone'
import { useVuelidate } from '@vuelidate/core'
import { helpers } from '@vuelidate/validators'
import { required } from '@/validations/index'

// Component
import { ElMessage } from 'element-plus'
import SectionTitle from 'components/SectionTitle.vue'
import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import GroupContainer from 'bbUi/components/group-container/group-container.vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import ExpandablePanel from 'bbUi/components/expandable-panel/expandable-panel.vue'
import MultipleEmailInput from 'components/MultipleEmailInput.vue'
import TextEditor from 'components/TextEditor.vue'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'
import PreviewEmail from 'components/EmailTemplate/PreviewEmailTemplate.vue'

export default defineComponent({
  components: {
    SectionTitle,
    Card,
    Button,
    Breadcrumb,
    GroupContainer,
    Input,
    Select,
    Checkbox,
    ExpandablePanel,
    MultipleEmailInput,
    TextEditor,
    PreviewEmail
  },
  async setup() {
    const {
      loading,
      actionShow,
      actionUpdate,
      actionUpdateStatus,
      state,
      stateUpdate,
      stateUpdateStatus
    } = useReviewEmail()
    const {
      data: formDataCompany,
      actionShow: actionShowCompany,
      loading: loadingCompany
    } = useCompanyProfile()
    const {
      formData: formDataEmailTemplate,
      loading: loadingEmailTemplateTheme,
      actionShow: showEmailTemplateTheme,
      actionUpdate: updateEmailTemplateTheme
    } = useEmailTemplateTheme('review-email')
    const {
      emailBody,
      isPreview,
      previewedTemplate,
      loading: loadingPreviewEmailTemplate,
      actionReviewEmail: actionPreviewEmailTemplate
    } = usePreviewEmailTemplate()
    
    const data = reactive({
      payment_method: {},
      email_wording: {
        textinfo: '',
        initialTextinfo: '',
        subject: '',
        bcc: []
      },
      web_link: '',
      is_rmail_on: 0,
      valid: false
    })

    const loadInitialData = async () => {
      await actionShow()
      if (state.value) {
        const initialData = deepClone(state.value)
        Object.assign(data, initialData)
        data.valid = data.web_link !== ''
        data.email_wording.initialTextinfo = data.email_wording.textinfo
      }
    }

    await loadInitialData()

    const refTextEditor = ref(null)

    const handleResetEmailTemplate = () => {
      data.email_wording.textinfo = data.email_wording.initialTextinfo

    }

    const handlePreviewEmailTemplate = async () => {
      emailBody.value = data.email_wording.textinfo
      await actionPreviewEmailTemplate()
    }

    const handleUpdateEmailTemplateTheme = async () => {
      await updateEmailTemplateTheme(() => {
        ElMessage({
          type: 'success',
          message: 'Review email theme updated'
        })
      })
    }
    const handleBccUpdate = (newBccData) => {
      data.email_wording.bcc = newBccData
    }

    const update = async () => {
      loading.update = true
      const isReadyEditor = refTextEditor.value.isEditorReady
      if (!isReadyEditor) return
      const isSaved = await refTextEditor.value.forceSave()
      if (!isSaved) return
      if (!validate()) {
        return
      }

      const updateData = {
        payment_methods: JSON.stringify(data.payment_method),
        textinfo: data.email_wording.textinfo,
        web_link: data.web_link,
        subject: data.email_wording.subject,
        bcc: data.email_wording.bcc
      }

      await actionUpdate(updateData)

      if (stateUpdate.value === 'updated') {
        ElMessage.success('Data has been saved!')
        await loadInitialData()
      }

      loading.update = false
    }

    const updateStatus = async () => {
      const value = data.is_rmail_on === 1 ? '1' : '0'
      await actionUpdateStatus({
        is_rmail_on: value
      })

      if (stateUpdateStatus.value === 'updated') {
        ElMessage.success(
          value === '1' ? 'Review Email Activated!' : 'Review Email Deactivated!'
        )
      }
    }

    const switchStatus = async (data) => {
      await updateStatus(data)
    }

    const rules = {
      web_link: {
        required: helpers.withMessage('Link is required', required)
      },
      email_wording: {
        subject: {
          required: helpers.withMessage('Subject is required', required)
        }
      }
    }

    const validation = useVuelidate(rules, data, {
      $lazy: true
    })

    const validate = async () => {
      const result = await validation.value.$validate()
      return result && !validation.value.$invalid
    }

    watch(
      () => state.value,
      (newValue) => {
        if (newValue) {
          const newData = deepClone(newValue)
          Object.assign(data, newData)
        }
      },
      { deep: true }
    )

    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Notifications',
        to: {
          name: 'settingEmailTemplate',
          query: {
            filter: 1
          }
        }
      },
      {
        text: 'Edit Notification'
      }
    ]

    const ckeditor = reactive({
      editor: ClassicEditor,
      config: {
        htmlSupport: {
          allow: [
            {
              styles: true,
              classes: true
            }
          ]
        }
      }
    })

    const handleCopyText = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        ElMessage({
          type: 'success',
          message: 'Text copied to clipboard'
        })
      })
    }

    const items = [
      { name: 'Days', value: 'D' },
      { name: 'Hours', value: 'H' },
      { name: 'Minutes', value: 'M' }
    ]

    const groupOrder = [
      'Digital Payouts',
      'Bank Transfers & Virtual Cards',
      'Cash & Manual Payouts',
      'Other'
    ]

    const groupDescriptions = {
      'Digital Payouts': 'Payouts made through apps, gift cards, and/or digital platforms.',
      'Bank Transfers & Virtual Cards': 'Payouts made through bank transfer and/or virtual cards.',
      'Cash & Manual Payouts': 'Payouts made through cash and/or checks.',
      'Other': 'All other payout methods that do no fit into the main categories.'
    }

    const sortedGroups = computed(() => {
      return Object.keys(data.payment_method).sort((a, b) => {
        const indexA = groupOrder.indexOf(a)
        const indexB = groupOrder.indexOf(b)
        return indexA - indexB
      })
    })

    await Promise.all([
      actionShowCompany(),
      showEmailTemplateTheme()
    ])

    return {
      loading,
      breadcrumb,
      data,
      update,
      switchStatus,
      items,
      filterNumber,
      validation,
      validate,
      formDataCompany,
      actionShowCompany,
      loadingCompany,
      handleBccUpdate,
      formDataEmailTemplate,
      refTextEditor,
      handleUpdateEmailTemplateTheme,
      handlePreviewEmailTemplate,
      loadingEmailTemplateTheme,
      loadingPreviewEmailTemplate,
      ckeditor,
      isPreview,
      previewedTemplate,
      handleCopyText,
      sortedGroups,
      groupOrder,
      groupDescriptions,
      handleResetEmailTemplate
    }
  }
})
</script>
<style>
.ck-editor__editable {
  height: 300px;
}
</style>